import React, { useState, useEffect } from 'react';
import { FaArrowLeft } from "react-icons/fa";
import { Link, useParams } from 'react-router-dom';
import './Country.css';
import styled from "styled-components";

const Page = styled.div`
  background-color: ${props => props.theme.mainColor};
  transition: all .5s ease;
`;

const Title = styled.h1`
    color: ${props => props.theme.titleColor};
    transition: all .5s ease;
`;

const Button = styled.button`
    background-color: ${props => props.theme.pageBackground};
    transition: all .5s ease;
    color:  ${props => props.theme.titleColor};
    box-shadow: 0 0 4px ${props => props.theme.shadowColor};
`;


const Country = () => {
    const [country, setCountry] = useState([])
    const { id } = useParams()
    const url = `https://restcountries.eu/rest/v2/alpha/${id}`;


    const fetchCountry = async () => {
        const response = await fetch(url);
        const country = await response.json();
        setCountry(country);
    }

    useEffect(() => {
        fetchCountry();
    }, [id]);


    
    return (
        <>
            <Page>
                <article key={country.name} >

                    <div className="country_profile">
                        <Link to={'/country-app'}>
                            <Button className="undefined_button">
                                <FaArrowLeft />
                                    Back
                            </Button>
                        </Link>
                        <Title>
                            <div className="country_info">
                                <img alt={country.name} src={country.flag} className="country_flag" />
                                <div className="country_info_detail">
                                    <h2>{country.name}</h2>
                                    <ul>
                                        <li><span>native Name: </span>{country.nativeName}</li>
                                        <li><span>population: </span>{country.population}</li>
                                        <li ><span>region: </span>{country.region}</li>
                                        <li><span>subregion: </span>{country.subregion}</li>
                                        <li><span>capital: </span>{country.capital}</li>
                                    </ul>

                                    <ul>
                                        <li><span>top Level Domain: </span>{country.topLevelDomain}</li>
                                        <li><span>currencies: </span>{country.currencies && country.currencies.map((currency, i) => {
                                            return (
                                                <span key={currency.code}>
                                                    {country.currencies.length === i + 1
                                                        ? currency.name
                                                        : currency.name + ", "}
                                                </span>
                                            );
                                        })}
                                        </li>
                                        <li><span>Languages: </span>{country.languages && country.languages.map((language, i) => {
                                            return (
                                                <span key={language.code}>
                                                    {country.languages.length === i + 1
                                                        ? language.name
                                                        : language.name + ", "}
                                                </span>
                                            );
                                        })}
                                        </li>
                                    </ul>

                                    <div className="borders">
                                        <h3>Border Countries:</h3>
                                        {country.borders && country.borders.map((border, i) => {
                                            return (
                                                <Link to={`/countries/${border.toLowerCase()}`}>
                                                <Button key={border.code} className="undefined_button">
                                                    {country.borders.length === i + 1
                                                        ? border
                                                        : border }
                                                </Button>
                                                </Link>
                                            );
                                        })}

                                    </div>
                                </div>
                            </div>
                        </Title>
                    </div>
                </article>

            </Page>
        </>

    )
}

export default Country
